.page-cruises {
  // border: $bred;
  width: 100%;
  padding: 122px 15px 105px;

  @media (max-width: 1023px) {
    padding: 100px 45px 85px;
  }

  @media (max-width: 767px) {
    padding: 67px 10px 50px;
  }
}

.page-cruises__inner {
  // border: $bred;
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
}

.page-cruises__inner h2 {
  font-weight: 600;
  font-size: 28px;
  line-height: 70%;
  text-align: center;
  margin-bottom: 45px;

  @media (max-width: 1023px) {
    font-size: 26px;
  }

  @media (max-width: 767px) {
    font-size: 24px;
    margin-bottom: 25px;
  }
}

.cruises__list {
  // border: $bred;
  display: flex;
  flex-wrap: wrap;
}

.cruises__item {
  // border: $bred;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  width: calc((100% - 120px) / 4);
  min-width: 260px;
  height: 386px;
  margin: 15px;
  padding: 35px 25px 30px 30px;
  color: #F9FBFD;
  background-blend-mode: multiply;

  @media (max-width: 1023px) {
    width: calc((100% - 60px) / 2);
    padding: 35px 25px 35px 35px;
  }

  @media (max-width: 767px) {
    width: 100%;
    padding: 35px 25px 25px;
  }
}

.cruises__item h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 70%;
}

.cruises__item h4 {
  font-weight: 500;
  font-size: 14px;
  line-height: 175%;
}

.cruises__item p {
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  margin-bottom: 9px;
}

.cruises__item button {
  margin-top: 17px;
  height: 40px;
  font-weight: 600;
  font-size: 14px;
  line-height: 70%;
}

.astoria__item {
  background: linear-gradient(180deg, rgba(1, 28, 64, 0) 50%, #011C40 100%), url("../img/astoria-cruise.jpg") center no-repeat;
  background-size: cover;
}

.aline__item {
  background: #011C40;
  justify-content: flex-start;
  padding: 35px 25px 30px;

  @media (max-width: 1023px) {
    padding: 35px 35px 30px;
  }

  @media (max-width: 767px) {
    padding: 35px 25px 30px;
  }

  h3 {
    margin-top: 0px;
    margin-bottom: 20px;
  }
}

